import { useState,useEffect } from 'react';
import './App.css';
import Square from './components/Square';

const initialState = ["","","","","","","","",""];


function App() {
  const [gameState,setGameState]=useState(initialState);
  const [isChanceOfX,setChanceOfX]=useState(true);


  const onBoxClicked=(index)=>{
    let states=Array.from(gameState);
    if(states[index]){
      return;
    }
    states[index]=isChanceOfX?"X":"O";
    setChanceOfX(!isChanceOfX);
    setGameState(states);
   
  }

  const clearGame=()=>{
    setGameState(initialState);
  }

  useEffect(()=>{
     const winner=whoIsWinner();
    
     if(winner){
      clearGame();
       alert(`Yeah! ${winner} is Win`)
     }
     

  },[gameState])

  const whoIsWinner = ()=>{
    const boxLines = [
      [0,1,2],
      [3,4,5],
      [6,7,8],
      [0,3,6],
      [1,4,7],
      [2,5,8],
      [0,4,8],
      [2,4,6]
    ];

    for(let i=0;i<boxLines.length;i++){
       const [a,b,c]=boxLines[i];
       if(gameState[a] && gameState[a] === gameState[b] && gameState[a] === gameState[c]){
         return gameState[a];
       }
       
    }
    return null;
  }

  return (
    <div className="App-header">
      <p className="heading">Tic Toc Toe</p>
      <div className="row">
        <Square  className="bottom-right" state={gameState[0]} onClick={()=>onBoxClicked(0)}/>
        <Square   className="bottom-right" state={gameState[1]} onClick={()=>onBoxClicked(1)}/>
        <Square className="box bottom" state={gameState[2]} onClick={()=>onBoxClicked(2)}/>
      </div>
      <div className="row">
        <Square className="bottom-right" state={gameState[3]} onClick={()=>onBoxClicked(3)}/>
        <Square  className="bottom-right" state={gameState[4]} onClick={()=>onBoxClicked(4)}/>
        <Square className="bottom" state={gameState[5]} onClick={()=>onBoxClicked(5)}/>
      </div>
      <div className="row">
        <Square  className="right" state={gameState[6]} onClick={()=>onBoxClicked(6)}/>
        <Square  className="right" state={gameState[7]} onClick={()=>onBoxClicked(7)}/>
        <Square state={gameState[8]} onClick={()=>onBoxClicked(8)}/>
      </div>
      <div className="clearBtn">
        <button onClick={clearGame}>Clear</button>
      </div>
    </div>
    
  );
}

export default App;
